import React from 'react';
import styles from './button.css';
import classnames from 'classnames';

type Props = {
  secondary?: boolean;
  children: any;
  className: string;
  onClick: Event;
  href: string;
};

export default function Button({
  className,
  secondary,
  children,
  href,
}: Props) {
  let Element: any = href ? 'a' : 'button';
  return (
    <Element
      href={href}
      className={classnames(
        className,
        styles.button,
        secondary && styles.secondary,
      )}
    >
      {children}
    </Element>
  );
}
